.banner-container {
        --bg-image: url('../../assets/images/home_banner.png');
        --bg-image-opacity:0.9;
        display: grid;
        place-content: right;
        text-align: left;
        padding-top: 20px;
        padding-bottom: 20px;
        margin: 10px 20px;
        position: relative;
        isolation: isolate;
}

.banner-container::after {
    content: '';
    position: absolute;
    z-index: -1;
    inset: 0;
    background-color: black;
    opacity: var(--bg-image-opacity);
    background-image: var(--bg-image);
    background-size: 140%;
    background-position: center right;
    border-radius: 10px;
}

.banner-title {
    font-family: 'Montserrat';
    font-size: 24px;
    font-weight: 500;
    color: white;
    padding: 5px 150px 5px 30px;
    line-height: 24px;
}


